@charset "UTF-8";
/* 基本樣式設置 */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* 禁用滾動條應只在需要的地方設置，而不是全局設置 */
body {
  overflow: hidden;
  position: relative;
}

/* html 和 body 的基本設置 */
html,
body {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

/* 開始頁面按鈕樣式 */
.foundButton {
  margin-bottom: 100px;
  padding: 50px 100px;
  border-radius: 10px;
  background: #d5f365;
  border: none;
  font-family: inherit;
  text-align: center;
  color: #2e3730;
  font-weight: bold;
  font-size: 2.25rem;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  bottom: 10px;
  left: 50%;
  transform: translateX(-50%);
}

/* 開始頁面圖片居中 */
.centered-image {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  width: 1000px;
  height: 500px;
  max-width: 100%;
  max-height: 100%;
}

/* 對話框樣式 */
.custom-modal {
  width: 80vw;
  max-width: 1000px;
  height: auto;
  max-height: 80vh;
  background-color: white;
  position: fixed;
  bottom: 100px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  align-items: flex-start;
  z-index: 1000;
  border-radius: 10px;
  padding: 0px;
}

.custom-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  z-index: 999;
}

.custom-modal-image {
  display: none;
}

.custom-modal-image-font {
  width: auto;
  left: 10%;
  height: 450px;
  position: absolute;
  z-index: 3000;
}

.custom-modal-content {
  text-align: center;
  align-content: center;
  font-size: 2.5rem;
  font-weight: bold;
  margin: auto;
  padding-left: 15%;
  color: darkgreen;
  width: 80%;
  height: 300px;
  flex-direction: column;
}

.custom-modal-button {
  border: 2px solid #2e3730;
  color: #2e3730;
  padding: 10px;
  margin-bottom: 30px;
  margin-right: 30px;
  cursor: pointer;
  align-self: flex-end;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100px;
  height: 50px;
  font-weight: bold;
  font-size: 1.3rem;
}

.custom-modal-title,
.custom-modal-overlay {
  display: none;
}

.custom-modal-countdown {
  text-align: center;
  font-size: 2.5rem;
  color: darkgreen;
  position: absolute;
  bottom: 15px;
  left: 40%;
}

body::before {
  content: "";
  position: fixed;
  top: 50%;
  left: 50%;
  width: 100vh;
  height: 100vw;
  background-image: inherit;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  z-index: -1;
  transform: translate(-50%, -50%);
  transform-origin: center;
}

/* 媒體查詢 */
@media screen and (max-width: 1000px) {
  body {
    transform: none;
    transform: rotate(90deg);
  }
  body::before {
    transform: translate(-50%, -50%);
  }
  .foundButton {
    margin-bottom: 8rem;
    padding: 2rem 4rem;
    font-size: 2.25rem;
    bottom: 3rem;
  }
  .centered-image {
    width: 80%;
    height: auto;
    left: 50%;
    transform: translateX(-50%);
    bottom: 1rem;
  }
  .custom-modal {
    width: 150%;
    height: 27vh;
    bottom: 25%;
    position: absolute;
    overflow: visible;
  }
  .custom-modal-content {
    align-content: center;
    font-size: 1.5rem;
    margin: auto;
    padding-left: 25%;
    width: 100%;
    height: 10rem;
    flex-direction: column;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .custom-modal-image-font {
    width: auto;
    height: 40vh;
    position: absolute;
    left: -10%;
    top: 57%;
    transform: translate(-50%, -50%);
    z-index: 3000;
  }
  .custom-modal-button {
    padding-right: 1rem;
    margin-bottom: 1.5rem;
    margin-right: 1rem;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 5.5rem;
    height: 2rem;
  }
  .custom-modal-countdown {
    text-align: center;
    font-size: 1rem;
    position: absolute;
    bottom: 1rem;
    left: 50%;
    transform: translateX(-50%);
  }
}/*# sourceMappingURL=index.css.map */