.game-container {
    text-align: center;
    padding: 20px;
    font-family: Arial, sans-serif;

    button {
        padding: 10px 20px;
        font-size: 16px;
        background-color: #4caf50;
        color: white;
        border: none;
        border-radius: 5px;
        cursor: pointer;
        margin-bottom: 20px;

        &:hover {
            background-color: #45a049;
        }
    }
}

.scanner-container {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.qr-reader {
    width: 100%;
    max-width: 600px;
    position: relative;
}

.align-box {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 200px;
    height: 200px;
    border: 2px dashed #ffffff;
    transform: translate(-50%, -50%);
}

.photo-container {
    margin-top: 20px;
    text-align: center;

    img {
        width: 100px;
        height: 100px;
        object-fit: cover;
        border: 2px solid #4caf50;
        border-radius: 5px;
    }
}